code[class*=language-],
pre[class*=language-] {
  color: #000;
  background: 0 0;
  font-family: Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre[class*=language-] {
  position: relative;
  margin: .5em 0;
  box-shadow: -1px 0 0 0 #358ccb,0 0 0 1px #dfdfdf;
  border-left: 10px solid #358ccb;
  background-color: #fdfdfd;
  background-image: linear-gradient(transparent 50%,rgba(69,142,209,.04) 50%);
  background-size: 3em 3em;
  background-origin: content-box;
  overflow: visible;
  padding: 0;
}

code[class*=language] {
  max-height: inherit;
  height: 100%;
  padding: 0 1em;
  display: block;
  overflow: auto;
}

:not(pre)>code[class*=language-],
pre[class*=language-] {
  background-color: #fdfdfd;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin-bottom: 1em;
}

:not(pre)>code[class*=language-] {
  position: relative;
  padding: .2em;
  border-radius: .3em;
  color: #c92c2c;
  border: 1px solid rgba(0,0,0,.1);
  display: inline;
  white-space: normal;
}

pre[class*=language-]:after,
pre[class*=language-]:before {
  content: '';
  z-index: -2;
  display: block;
  position: absolute;
  bottom: .75em;
  left: .18em;
  width: 40%;
  height: 20%;
  max-height: 13em;
  box-shadow: 0 13px 8px #979797;
  -webkit-transform: rotate(-2deg);
  -moz-transform: rotate(-2deg);
  -ms-transform: rotate(-2deg);
  -o-transform: rotate(-2deg);
  transform: rotate(-2deg);
}

:not(pre)>code[class*=language-]:after,
pre[class*=language-]:after {
  right: .75em;
  left: auto;
  -webkit-transform: rotate(2deg);
  -moz-transform: rotate(2deg);
  -ms-transform: rotate(2deg);
  -o-transform: rotate(2deg);
  transform: rotate(2deg);
}

.token.block-comment,
.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
  color: #7D8B99;
}

.token.punctuation {
  color: #5F6364;
}

.token.boolean,
.token.constant,
.token.deleted,
.token.function-name,
.token.number,
.token.property,
.token.symbol,
.token.tag {
  color: #c92c2c;
}

.token.attr-name,
.token.builtin,
.token.char,
.token.function,
.token.inserted,
.token.selector,
.token.string {
  color: #2f9c0a;
}

.token.entity,
.token.operator,
.token.url,
.token.variable {
  color: #a67f59;
  background: rgba(255,255,255,.5);
}

.token.atrule,
.token.attr-value,
.token.class-name,
.token.keyword {
  color: #1990b8;
}

.token.important,
.token.regex {
  color: #e90;
}

.language-css .token.string,
.style .token.string {
  color: #a67f59;
  background: rgba(255,255,255,.5);
}

.token.important {
  font-weight: 400;
}

.token.bold {
  font-weight: 700;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.namespace {
  opacity: .7;
}

.token.cr:before,
.token.lf:before,
.token.tab:not(:empty):before {
  color: #e0d7d1;
}

pre[class*=language-].line-numbers {
  padding-left: 0;
}

pre[class*=language-].line-numbers code {
  padding-left: 3.8em;
}

pre[class*=language-].line-numbers .line-numbers-rows {
  left: 0;
}

pre[class*=language-][data-line] {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
}

pre[data-line] code {
  position: relative;
  padding-left: 4em;
}

pre .line-highlight {
  margin-top: 0;
}

@media screen and (max-width:767px) {
  pre[class*=language-]:after,
  pre[class*=language-]:before {
    bottom: 14px;
    box-shadow: none;
  }
}